    /* src/components/Header/Header.css */
    i.Nav {
        background-color: #fff;
        border-bottom: 1px solid rgba(0, 0, 0, 0.0975);
        position: fixed;
        top: 0;
        width: 100%;
        z-index: 2;
        -webkit-transition: height 0.2s ease-in-out;
        transition: height 0.2s ease-in-out;
        height: 77px;
      }
      .Nav-menus {
        display: flex;
        flex-direction: row;
        height: 77px;
        width: 100%; /* 70% - сместит правее*/
        margin: 0 auto;
        padding: 26px 60px;
      }
      .Nav-brand-logo {
        display: block;
        /* background-position: -176px 0px; */
        background-position: -176px 0px;
        /* background-image: url(https://sun9-45.userapi.com/Wut2zGYiGJtcqrvVrmQkXsuOvkrHm6rSdNsu3w/SFobWTBeonE.jpg); */
